@import "https://fonts.googleapis.com/css2?family=Orbitron:wght@500;700&family=Poppins:wght@300;400;600&display=swap";
* {
  -webkit-user-select: none;
  user-select: none;
  caret-color: #0000;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  background-color: #161616;
  height: 100%;
  font-family: Poppins, sans-serif;
  overflow: hidden;
}

h1, h2, h3, h4, h5, h6 {
  letter-spacing: 1px;
  color: #fff;
  font-family: Orbitron, sans-serif;
  font-weight: 700;
}

p {
  color: #fff;
  -webkit-user-select: none;
  user-select: none;
  caret-color: #0000;
  line-height: 1.5;
}

.button, #enterSystem, .arrow {
  -webkit-user-select: none;
  user-select: none;
  caret-color: #0000;
  cursor: pointer;
  font-family: Orbitron, sans-serif;
  font-weight: 500;
}

.sections * {
  box-sizing: border-box;
  width: 100vw;
  height: 100vh;
  display: block;
}

::-webkit-scrollbar {
  width: 4px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: none;
}

::-webkit-scrollbar-thumb {
  background-color: #888;
  background-clip: content-box;
  border: 2px solid #0000;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: none;
}
